<template>
  <div class="timer">
    <h1>⏳ Қолган вақт:</h1>
    <p>{{ formatTime }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      weddingDate: new Date('2025-06-22 00:00:00'),
      timeLeft: '',
    };
  },
  computed: {
    formatTime() {
      const now = new Date();
      const diff = this.weddingDate - now;
      
      const years = Math.floor(diff / (1000 * 60 * 60 * 24 * 365));
      const months = Math.floor((diff % (1000 * 60 * 60 * 24 * 365)) / (1000 * 60 * 60 * 24 * 30));
      const days = Math.floor((diff % (1000 * 60 * 60 * 24 * 30)) / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));

      return `${years} йил ${months} ой ${days} кун ${hours} соат ${minutes} дақиқа`;
    },
  },
  mounted() {
    setInterval(() => {
      this.timeLeft = this.formatTime;
    }, 60000);
  },
};
</script>

<style>
.timer {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  padding: 20px;
  background: #fce4ec;
  border-radius: 10px;
}
</style>